import * as dataForge from 'data-forge';

export default function findAnswer(question){

  const df = dataForge.fromCSV(window.northwest)

  //console.log(df.head(5).toString());
  console.log(question)

  let answerDf = df.where(row => 
    //row['Total Offenses per Person'] == '1' &&
    //row['Expungeable'] != 'No' &&
    //parseInt(row['Age at Offense']) < 21
    (question.maxOffenses === '' ||
    parseInt(row['Total Offenses per Person']) <= parseInt(question.maxOffenses)) &&
    (question.maxIncidents === '' || 
    parseInt(row['Incidents per Person']) <= parseInt(question.maxIncidents)) &&
    (question.sexIneligible === false || 
    row['sex'] === '0') &&
    (question.murderIneligible === false || 
    row['murder'] === '0') &&
    (question.guiltyIneligible === false || 
    row['guilty'] == 'False') &&
    (question.ineligibleIneligible === false || 
    row['Expungeable'] !== 'No') &&
    (question.juvenileOnly === false || 
    parseInt(row['Age at Offense']) < 21)
  );

  let answerDf2 = df.where(row =>
    question.maxOffenses === '' &&
    question.maxIncidents === '' &&
    question.sexIneligible === false &&
    question.murderIneligible === false &&
    question.guiltyIneligible === false &&
    question.ineligibleIneligible === false &&
    question.juvenileOnly === false
  );

  // To get the number of individuals, duplicate person ID's should be removed
  answerDf = answerDf.distinct(row => row['Person ID']);
  return answerDf.count();

  // YOU MUST ACCOUNT FOR DOUBLE COUNTING OF PERSON IDENTIFIERS
  // NEED TO COUNT UNIQUE PERSON IDENTIFIERS IN THE ANSWER
}

