import React from 'react';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';

import findAnswer from '../functions/findanswer';

const QuestionEditor = ({ question, question_index, updateQuestion, getEmptyEdits }) => {

  function handleSubmit(event){
    event.preventDefault();
    console.log('Submitting edits');

    question.name = question.edits.name;
    question.maxOffenses = question.edits.maxOffenses;
    question.maxIncidents = question.edits.maxIncidents;
    question.sexIneligible = question.edits.sexIneligible;
    question.murderIneligible = question.edits.murderIneligible;
    question.guiltyIneligible = question.edits.guiltyIneligible;
    question.ineligibleIneligible = question.edits.ineligibleIneligible;
    question.juvenileOnly = question.edits.juvenileOnly;

    question.edits = getEmptyEdits(question);
    question.editing = false;

    // When edits to a question are submitted, get the answer to the modified question
    let answer = findAnswer(question);
    console.log(answer);

    updateQuestion(question_index, question);
  }
  
    const nameInput = <TextField
      variant='outlined'
      margin='normal'
      value={question.edits.name}
      helperText='question name'
      onChange={(event) => {
        question.edits.name = event.target.value;
        updateQuestion(question_index, question);
      }}
    />

    const maxOffensesInput = <TextField
      variant='outlined'
      margin='normal'
      value={question.edits.maxOffenses}
      type='number'
      helperText='max offenses'
      onChange={(event) => {
        question.edits.maxOffenses = event.target.value;
        updateQuestion(question_index, question);
      }}
    />

    const maxIncidentsInput = <TextField
      variant='outlined'
      margin='normal'
      value={question.edits.maxIncidents}
      type='number'
      helperText='max incidents'
      onChange={(event) => {
        question.edits.maxIncidents = event.target.value;
        updateQuestion(question_index, question);
      }}
    />

    const sexInput = <FormControlLabel
      control={
        <Checkbox
          checked={question.edits.sexIneligible}
          name='sexInput'
          onChange={(event) => {
            console.log('sex checkbox changed');
            question.edits.sexIneligible = (question.edits.sexIneligible) ? false : true;
            updateQuestion(question_index, question);
          }}
        />
      }
      label='sex'
    />

    const murderInput = <FormControlLabel
      control={
        <Checkbox
          checked={question.edits.murderIneligible}
          name='murderInput'
          onChange={(event) => {
            console.log('murder checkbox changed');
            question.edits.murderIneligible = (question.edits.murderIneligible) ? false : true;
            updateQuestion(question_index, question);
          }}
        />
      }
      label='murder'
    />

    const ineligibleInput = <FormControlLabel
      control={
        <Checkbox
          checked={question.edits.ineligibleIneligible}
          name='ineligibleInput'
          onChange={(event) => {
            console.log('ineligible checkbox changed');
            question.edits.ineligibleIneligible = (question.edits.ineligibleIneligible) ? false : true;
            updateQuestion(question_index, question);
          }}
        />
      }
      label='ineligible'
    />

    const guiltyInput = <FormControlLabel
      control={
        <Checkbox
          checked={question.edits.guiltyIneligible}
          name='guiltyInput'
          onChange={(event) => {
            console.log('guilty checkbox changed');
            question.edits.guiltyIneligible = (question.edits.guiltyIneligible) ? false : true;
            updateQuestion(question_index, question);
          }}
        />
      }
      label='guilty'
    />

    const juvenileInput = <FormControlLabel
      control={
        <Checkbox
          checked={question.edits.juvenileOnly}
          name='juvenileOnly'
          onChange={(event) => {
            console.log('juvenile checkbox changed');
            question.edits.juvenileOnly = (question.edits.juvenileOnly) ? false : true;
            updateQuestion(question_index, question);
          }}
        />
      }
      label='juvenile'
    />

    const submitButton = <Button
      variant='contained'
      type='submit'
    >
      Submit Edits
    </Button>

    return (
      <div>
        
        <form onSubmit={(event) => handleSubmit(event)}>
          {nameInput}
          {maxOffensesInput}
          {maxIncidentsInput}
          {sexInput}
          {murderInput}
          {guiltyInput}
          {ineligibleInput}
          {juvenileInput}
          {submitButton}
        </form>

      </div>
    );
  };
  
  export default QuestionEditor;