import React, { useState } from 'react';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';

import QuestionEditor from "./QuestionEditor"

const Question = ({ question, question_index, moveQuestion, deleteQuestion, updateQuestion, getEmptyEdits }) => {
    //const [questions, setQuestions] = useState([]);

    const question_editor = <QuestionEditor 
      question={question} 
      question_index={question_index} 
      updateQuestion={updateQuestion}
      getEmptyEdits = {getEmptyEdits}
    />
  
    return (
      <div>
        <Paper>
          {question_index + 1}
          {question.name}

          <IconButton onClick={() => {
            if(question.editing){
              question.editing = false;
              question.edits = getEmptyEdits(question);
            } else {
              question.editing = true;
            }
            updateQuestion(question_index, question);
          }}>
            <EditIcon />
          </IconButton>
          <IconButton onClick={() => {
            moveQuestion(question_index, 'upwards');
          }}>
            <KeyboardArrowUpIcon />
          </IconButton>
          <IconButton onClick={() => {
            moveQuestion(question_index, 'downwards');
          }}>
            <KeyboardArrowDownIcon />
          </IconButton>
          <IconButton onClick={() => {
            deleteQuestion(question_index);
          }}>
            <DeleteIcon />
          </IconButton>

        {question.editing && question_editor}

        </Paper>

      </div>
    );
  };
  
  export default Question;